import styled from 'styled-components';
import { Container } from 'reactstrap';

export const SearchContainer = styled(Container)`
  padding-left: 10px;
  padding-right: 10px;

  button {
    font-size: 13px;
    padding: 5px;
    line-height: 0.8;
    width: 64px;

    span {
      font-size: 24px;
      line-height: 0.1;
      font-weight: bold;
    }
  }
`;
